import mpvuePicker from '@/components/mpvue-picker/mpvuePicker.vue';
import mpvueCityPicker from '@/components/mpvue-citypicker/mpvueCityPicker.vue'
export default {
  data () {
    return {
      disabled: false,
      list: '',
      rate: 0,
      cityPickerValueDefault: [0, 0,1],
			pickerValueDefault: [0,0],
			pickerValueArray:[],
      pickerText: '请选择城市',
      areaCode: '',
      showCode: false,
      codeDisable: false,
      request_no: '',
      code: '',
      url: ''
    };
  },
  components: {
    mpvuePicker,
		mpvueCityPicker,
  },
  computed: {},
  methods: {
    showPicker(){
			this.$refs.mpvuePicker.show()
		},
		onConfirm(e){
      this.pickerText = e.label
      this.areaCode = e.value[1]
    },
    cancle(){
      this.showCode = false
      uni.showToast({
        title: '您取消了提现！',
        icon: ''
      })
    },
    confirm(){
      if(!this.code){
        uni.showToast({
          title: '请输入验证码',
          icon: 'none'
        })
        return
      }
      this.codeDisable = true
      uni.request({
        url: url, //仅为示例，并非真实接口地址。
        data: {
          request_no: this.request_no,
          smsCode: this.code,
          token: uni.getStorageSync('token'),
        },
        method: 'POST',
        header: {
          'content-type': 'application/x-www-form-urlencoded',
          'token': uni.getStorageSync('token'),
        },
        success: (res) => {
          if(res.data.code==1){
            uni.showModal({
              title: '确认信息',
              content: res.data.msg,
              success: (d)=>{
                if(d.confirm){
                  uni.navigateTo({
                    url: '/pages/index/merchant-receiving/recode'
                  })
                }else{
                  uni.showToast({
                    title: '你取消了提现',
                    icon: 'none'
                  })
                }
              }
            })
          }
        }
      });
    }, 
    submit(){
      if(!this.areaCode && this.list.hasarea){
        uni.showToast({
          title: '请选择城市',
          icon: 'none'
        })
        return
      }
      let data = {
        channel: this.list.channel,
        amount: this.list.amount,
        deposit_id: this.list.deposit_id,
        credit_id: this.list.credit_id,
        city_code: this.areaCode
      }
      this.$store.dispatch('setCashierOrder',data).then(res=>{
        if(res.paySMS==1){
          this.showCode = true
          this.request_no = res.request_no
          this.url = res.action
        }else{
          uni.showToast({
            title: '提现成功'
          })
        }
      }).catch(err=>{
        if(err.code==5) {
          uni.setStorageSync('orderSn',err.request_no)
          uni.setStorageSync('url',err.url)
          uni.navigateTo({
            url: '/pages/index/merchant-receiving/pay'
          })
        }
      })
    }
  },
  onLoad(){
    this.list = uni.getStorageSync('order')
    this.$store.dispatch('getCashierRate',{
      amount: this.list.amount
    }).then(res=>{
      this.rate = res.data
    }).catch(err=>{})
    this.$nextTick(() => {
      if(this.list.hasarea==1){
        this.$store.dispatch('getCashierArea',{
          channel: this.list.channel
        }).then(res=>{
          this.pickerValueArray = res.data
          this.$refs.mpvuePicker.show()
        }).catch(err=>{})
      }
    })
  }
}

